import Mock, { Random } from 'mockjs'
import FetchMock from 'fetch-mock';

export default {
    init: function () {

        FetchMock.get('/dashboard', Mock.mock({
            'summary': [
                {
                    field: '',
                    value: 90,
                },
                {
                    field: '',
                    value: 190
                },
                {
                    field: '',
                    value: 90
                },
                {
                    field: '',
                    value: 290
                },
                {
                    field: '',
                    value: 290
                },
                {
                    field: '',
                    value: 290
                },
                {
                    field: '',
                    value: 290
                }
            ],
            'kongyuqi': {
                name: '',
                pollution_rate: 30.01,
                values: [
                    {
                        field: 'A',
                        value: 90,
                    },
                    {
                        field: 'B',
                        value: 190
                    },
                    {
                        field: 'A',
                        value: 90
                    },
                    {
                        field: 'B',
                        value: 290
                    }
                ]
            }
        }));

        // fetch
        FetchMock.once('*', (url, options) => {
            FetchMock.restore();
            return fetch(url, options);
        });
    }
}


